.location-container{
    max-width: 1110px;
    margin: 0 auto;
    padding: 96px 0 160px 0; 
    @include pad{
      padding: 60px 39px 96px 39px;
    }
    @include mobile{
      padding: 28px 15px 56px 15px;
    }
  }
  .locationTitle{
    border-left: 8px solid $primary;
    padding-left: 16px;
    margin-bottom: 60px;
    @include pad{
      border-left: 4px solid $primary;
      margin-bottom: 40px;
    }
    h2{
      width: 192px;
      font-size: 48px;
      line-height: 80px;
      color:#495057;
    }
  }
  .selectStore{
    display: flex;
    align-items: center;
    margin-bottom: 32px;
    p{
      font-size: 20px;
      line-height: 30px;
      @include pad{
        font-size: 16px;
      }
      @include mobile{
        width: 20%;
        font-size: 14px;
      }
      
    }
    .stores{
      margin-left: 22px;
      width: 29%;
      padding: 9px 19px;
      @include pad{
        padding: 12px 16px;
        width: 31%;
      }
      @include mobile{
        margin-left: 12px;
        width: 80%;
      }
      option{
        font-size: 18px;
        line-height: 1.5;
        padding: 9px 19px;
      }
    }
  }
  
  
  .infoMap{
    // 更改處
    display: flex;
    flex-wrap: wrap;
    @include pad{
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }
  
  .siteCard{
    //更改處
    width: 30%;
    margin-right: 30px;
    margin-bottom: 32px ;
    &:nth-last-child(1),&:nth-last-child(2){
      margin-bottom: 0;
    }
    @include pad{
      // display: flex;
      width: 46%;
      &:nth-child(even){
        margin-right: 0px;
      }
    }
    @include mobile{
      width: 100%;
      margin-right: 0px;
      &:nth-last-child(2){
      margin-bottom: 32px;
     }
    }
    img{
      width: 100%;
      height:224px;
      @include pad{
        width: 100%;
        height: auto;
      }
      @include mobile{
        width: 100%;
        margin-right: 0;
      }
    }
  }
  
  
  .sitecardBody{
    @include pad{
      //更改處
      width: 100%;
    }
    h3{
    font-size: 24px;
    line-height: 1.5;
    padding: 12px 24px;
    border: 1px solid $shadow-color ;
    @include pad{
      font-weight: bold;
      font-size: 16px;
      line-height: 1.5;
      padding: 8px 16px;
    }
   }
  }
  
  .sitecardBody ul{
    padding: 16px 26px;
    border: 1px solid $shadow-color ;
    @include pad{
      padding: 16px 24px;
    }
    li{
      // width: 100%;
      &:nth-child(1),&:nth-child(2){
        margin-bottom: 8px;  
      }
      
    }
  }
  
  .sitecardBody a{
    display: flex;
    align-items: center;
    color: black;
    img{
      margin-right: 10px;
      @include size(18px,18px);
        }
    p{
      width: 100%;
     font-size: 20px;
      line-height: 30px; 
      @include pad{
        font-size: 14px;
        line-height: 1.5;
      }
    }
  }
  
  
  .sitecardBody .locationBtn{
      justify-content: center;
      font-size: 19px;
      line-height: 1.5;
      padding: 20px 0;
      background-color: #000;
      color: white;
    }
  